// import './App.css';
import { useEffect } from 'react';
import Graph from "graphology";
import { parse } from "graphology-gexf/browser";
import { SigmaContainer, useLoadGraph, ControlsContainer, ZoomControl, FullScreenControl, SearchControl } from "@react-sigma/core";
import EdgesDefaultProgram from 'sigma/rendering/webgl/programs/edge';
import EdgesFastProgram from 'sigma/rendering/webgl/programs/edge.fast';
import "@react-sigma/core/lib/react-sigma.min.css"

function App() {
  const LoadGraph = () => {
    const loadGraph = useLoadGraph();

    const fetchGraph = async () => {
      try {
        const res = await fetch("http://localhost:8000/");
        const gexf = await res.text();
        console.log(gexf);
        return parse(Graph, gexf);
      } catch (error) {
        console.log(error);
        return error;
      }
    };

    useEffect(() => {
      fetchGraph().then((res) => {
        loadGraph(res);
      })
    }, [loadGraph]);

    return null;
  };

  return (
      <SigmaContainer
          style={{ height: "100vh", width: "100%" }}
          settings={{
            minCameraRatio: 0.1,
            maxCameraRatio: 1,
            defaultEdgeType: 'edges-fast',
            edgeProgramClasses: {
              'edges-default': EdgesDefaultProgram,
              'edges-fast': EdgesFastProgram
            },
            hideEdgesOnMove: false,
            hideLabelsOnMove: true,
            renderLabels: false
          }}
      >
        <LoadGraph />
        <ControlsContainer position={"bottom-right"}>
          <ZoomControl />
          <FullScreenControl />
        </ControlsContainer>
        <ControlsContainer position={"top-right"}>
          <SearchControl style={{ width: "200px" }} />
        </ControlsContainer>
      </SigmaContainer>
  );
}

export default App;
